#gif {
	background-image: url('/img/cat1.webp');
	background-position: center;
	background-repeat: no-repeat;
	width: 200px;
	height: 200px;
    margin: auto;
	animation: giffy 0.3s infinite steps(1);
	animation-delay: 0s;
    border-radius: 100px;
}

.playing {
	animation-play-state:running !important;
}

.paused {
	animation-play-state:paused !important;
}

@keyframes giffy {
	0%   { background-image: url('/img/cat1.webp'); } 
	12%   { background-image: url('/img/cat2.webp'); } 
	24%   { background-image: url('/img/cat3.webp'); } 
	36%   { background-image: url('/img/cat4.webp'); } 
	48%   { background-image: url('/img/cat5.webp'); } 
	60%   { background-image: url('/img/cat6.webp'); } 
	72%   { background-image: url('/img/cat7.webp'); } 
	84%   { background-image: url('/img/cat8.webp'); } 
	94%   { background-image: url('/img/cat9.webp'); } 
}

button{
	border: 0px;
	height: 30px;
    margin-top: 10px;
	margin-bottom: 5px;
    margin-left: 5px;
    margin-right: 5px;
	border-radius: 100px;
	padding: 10px;
	font-size: 10px;
}

body::after{
	position:absolute; width:0; height:0; overflow:hidden; z-index:-1; 
	content:url('/img/cat1.webp') url('/img/cat2.webp') url('/img/cat3.webp') url('/img/cat4.webp') url('/img/cat5.webp') url('/img/cat6.webp') url('/img/cat7.webp') url('/img/cat8.webp') url('/img/cat9.webp');
 }

#title{
    margin: 10px;
	visibility: hidden;
}